

.center {
	justify-content: center;
	align-items: center;
	display: flex; }

.search-container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100px;
	background-color: #F3F3F3;
	padding: 30px; }


.flex-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-end; }

.black {
	background-color: black; }


.search-button {
	margin-left: 10px;
	padding: 5px;
	display: inline-block;
	border-style: solid;
	border-color: black;
	border-width: 1px;
	border-radius: 5px;
	background-color: white; }

.search-button:hover {
	cursor: pointer; }

.search-button:active {
	background-color: #F3F3F3; }

.search-input {
	margin-right: 10px;
	padding: 5px;
	font-size: 16px; }

